<script>
export default {
  name: 'BillDetail',
  data() {
    return {
      fromReportView: false
    }
  },
  created() {
    // 获取来源信息
    this.fromReportView = this.$route.query.fromReportView === 'true';
  },
  methods: {
    goBack() {
      // 返回我的页面，并保持状态
      this.$router.push({
        path: '/my-page',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    goToHomePage() {
      // 根据来源返回相应的首页状态
      this.$router.push({
        path: '/home',
        query: { 
          showReportPage: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    goToMyPage() {
      // 返回我的页面，并保持状态
      this.$router.push({
        path: '/my-page',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    goToExpenseDetail() {
      // 跳转到费用明细页面
      this.$router.push({
        path: '/expense-detail',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false',
          amount: '285.00',
          location: '附二门诊CT扫描室',
          time: '上午10:36',
          date: '2024年5月18日'
        }
      });
    }
  }
}
</script>

